@font-face {
  font-family: "Neue Montreal";
  src: url(../Assets/NeueMontreal-Regular.otf);
}
@font-face {
  font-family: "Oregon LDO";
  src: url("../Assets/Oregon LDO Light.ttf");
}
* {
  margin: 0;
  padding: 0;
}

body {
  background: #000;
  font-family: "Neue Montreal";
}

.home {
  overflow: hidden;
}

nav {
  position: fixed;
  top: 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
  z-index: 2;
}

.menu__items-link {
  padding: 40px;
}
.menu__items-link a {
  color: #fff;
  font-size: 14px;
  font-weight: 400;
  text-decoration: none;
}

.menu__logo {
  padding: 40px;
}
.menu__logo a {
  color: #fff;
  font-size: 14px;
  text-transform: uppercase;
  text-decoration: none;
}

.menu__items {
  display: flex;
}
.menu__items-link.active a {
  text-decoration: underline;
}

.container {
  margin-top: 150px;
  margin-left: 200px;
}
.container__item {
  position: relative;
  width: 540px;
  height: 360px;
  margin: 50px 500px 20px 80px;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}
.container__item-title a {
  color: #fff;
  text-decoration: none;
}
.container__item h1 {
  position: absolute;
  left: 40px;
  bottom: -40px;
  font-family: "Oregon LDO";
  font-weight: lighter;
  font-size: 64px;
  color: #fff;
}
.container__item-header h1 {
  position: relative;
  left: -100px;
  top: -100px;
  width: 600px;
  height: 360px;
  font-family: "Oregon LDO";
  font-weight: lighter;
  font-size: 100px;
  color: #fff;
}
.container__item-1 {
  background-image: url("../Assets/img-one.jpeg");
}
.container__item-2 {
  background-image: url("../Assets/img-two.jpeg");
}
.container__item-3 {
  background-image: url("../Assets/img-three.jpeg");
}
.container__item-4 {
  background-image: url("../Assets/img-four.jpeg");
}
.container__item-5 {
  background-image: url("../Assets/img-five.jpeg");
}

@media (max-width: 900px) {
  .container__item {
    width: 300px;
    height: 200px;
    margin: 50px 100px 20px 80px;
  }
  .container__item-header h1 {
    font-size: 30px !important;
    left: -50%;
  }
  .container__item h1 {
    font-size: 40px;
    bottom: -20px;
  }
}